@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;500;600;800&display=swap');
@font-face {
  font-family: 'Sprite';
  src: url('./SCSS/fonts/Sprite\ Graffiti.otf')
}
@font-face {
  font-family: 'Sprite Extras';
  src: url('./SCSS/fonts/Sprite\ Graffiti\ Extras.otf')
}
@import 'SCSS/_theme.scss';

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  background-color: $dark;
  overflow-anchor: none;
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

